<template>
  <KeyKeepAlive :include="openedLocationKeys">
    <component ref="tab" :is="component" :key="tabMgr.get().location" :value="tabMgr.get()" :tab="tabMgr.get()"/>
  </KeyKeepAlive>
</template>

<script>
import KeyKeepAlive from '@/render/plugins/keyKeepAlive.js'

export default {
  components: {
    KeyKeepAlive
  },
  name: 'TK2',
  data () {
    return {
      transitionName: 'fade-in'
    }
  },

  computed: {
    component () {
      console.log('вкладка', this.tabMgr.get().component)
      return () => import(`@/render/tabs/${this.tabMgr.get().component}.vue`)
    },
    openedLocationKeys () {
      return this.tabMgr.list.map(v => v.location);
    }
  }
}
</script>
