<template>
  <v-app-bar
  app
  clipped-right
  clipped-left
  flat
  height="32"
  >
    <v-tabs v-model="tabMgr.index" align-with-title show-arrows>
      <v-tabs-slider class="accent lighten-4"></v-tabs-slider>
      <v-tooltip bottom v-for="(item, index) in tabMgr.list" :key="index">
        <template v-slot:activator="{ on, attrs }">
          <v-tab v-bind="attrs" v-on="on" @dblclick="reload" v-longclick="reload" :style="item.color ? `border-top: 2px solid ${item.color};` : ''">
            {{ item.title }}
            <v-btn small icon @click="() => tabMgr.close(index)"><v-icon small>mdi-close</v-icon></v-btn>
          </v-tab>
        </template>
        <span>{{item.fullTitle || item.title}}</span>
      </v-tooltip>
    </v-tabs>
</v-app-bar>
</template>

<script>
export default {
  methods: {
    reload () {
      this.$emit('reload')
    }
  }
}
</script>
