import Vue from 'vue';
import api from './api.js';
import tkb from './tkb.js';

class Core {
  constructor() {
    this.mobile = window.innerWidth < 600;
    window.addEventListener('resize', () => { this.mobile = window.innerWidth < 600; });
  }

  get state() {
    const results = {
      api: {
        not_authorized: { type: 'auth' },
        token_check: { type: 'loading', data: 'Восстановление сессии в биллинге...' },
        authorization: { type: 'loading', data: 'Авторизация в биллинге...' }
      },
      tkb: {
        not_authorized: { type: 'auth' },
        authorization: { type: 'loading', data: 'Авторизация в TKB...' }
      }
    };

    return results.api[api.status] || results.tkb[tkb.status] || { type: 'main' };
  }
}

const core = new Core();
Vue.mixin({
  data: () => ({ core })
});

export default core;
