import Vue from 'vue';

const tabMgr = {};
tabMgr.index = 0;
tabMgr.list = [];

tabMgr.open = function (rawLocation, params = null, goto = true) {
  const location = params ? `${rawLocation}?${new URLSearchParams(params)}` : rawLocation;
  const foundTab = tabMgr.list.find(v => v.location === location);
  if (foundTab) {
    if (goto) {
      tabMgr.index = tabMgr.list.indexOf(foundTab);
    }

    return foundTab;
  }

  const splitIndex = location.indexOf('?');
  const tab = {
    location,
    component: splitIndex === -1 ? location : location.substring(0, splitIndex),
    // eslint-disable-next-line max-len
    query: splitIndex !== -1 ? Object.fromEntries(new URLSearchParams(location.substring(splitIndex))) : {},
    saved: null,
    title: splitIndex === -1 ? location : location.substring(0, splitIndex),
    fullTitle: location
  };

  tabMgr.list.push(tab);

  if (goto) {
    tabMgr.index = tabMgr.list.length - 1;
  }

  return tab;
};

tabMgr.close = function (rawIndex = null) {
  const index = rawIndex === null ? tabMgr.list.indexOf(tabMgr.list[tabMgr.index]) : rawIndex;
  if (index === -1) {
    return;
  }

  tabMgr.list.splice(index, 1);
};

tabMgr.get = function () {
  const tab = tabMgr.list[tabMgr.index] || tabMgr.list[0];
  if (!tab) {
    return tabMgr.open('Overview');
  }

  return tab;
};

setInterval(() => {
  tabMgr.list.filter(v => v.tick).forEach(v => {
    if (v.tick.filter.includes('d') && !document.hasFocus()) {
      v.tick.waitTick = true;
      return;
    }

    if (v.tick.filter.includes('t') && tabMgr.get() !== v) {
      v.tick.waitTick = true;
      return;
    }

    v.tick.waitTick = false;
    v.tick.callback(false);
  });
}, 10000);

Vue.mixin({
  data: () => ({ tabMgr })
});

export default tabMgr;