import Vue from 'vue';
import UarhApiClient from 'uarh-api-client';

class Tkb {
  constructor() {
    this.status = 'not_authorized';
    // this.url = 'https://tkb.tk2.u14t.ru/method';
    this.client = new UarhApiClient({
      baseUrl: 'https://tkb.tk2.u14t.ru/api/',
      token: this.token
    });

    if (this.token) {
      this.checkToken();
    }

    setInterval(() => {
      if (this.status !== 'authorized') {
        return;
      }

      this.query('put', 'staff/shift').catch(() => { this.status = 'not_authorized'; });
    }, 60e3);
  }

  get token() {
    return localStorage.tkbToken || '';
  }

  set token(newValue) {
    console.log(newValue)
    this.client.options.token = newValue;
    localStorage.tkbToken = newValue;
  }

  async checkToken() {
    this.status = 'authorization';
    const result = await this.query('put', 'staff/shift')
      .then(() => true)
      .catch(() => false);

    if (!result) {
      this.status = 'not_authorized';
      return;
    }

    this.status = 'authorized';
  }

  async query(method, route, body = {}) {
    return this.client.query(method, route, body);
  }
  /* async func(func, params = {}) {
    const response = await fetch(`${this.url}?${new URLSearchParams({ func, token: localStorage.tkbToken, ...params })}`).then(v => v.json());
    if (response.error) {
      throw Error(response.error);
    }

    return response.data;
  } */
}

const tkb = new Tkb();
Vue.mixin({
  data: () => ({ tkb })
});

export default tkb;
