import tkb from './tkb';

export default class IpService {
  constructor(root) {
    this.root = root;
    this.cache = {};
  }

  get(ip) {
    if (!this.cache[ip]) {
      this.cache[ip] = {
        alive: false,
        services: [],
        loading: true,
        ip
      };
    }

    this.update(this.cache[ip]);
    return this.cache[ip];
  }

  async update(ipData) {
    ipData.loading = true;
    Object.assign(ipData, await tkb.query('get', `util/ip/${ipData.ip}`));
    ipData.loading = false;
  }
}
