import tabMgr from '../plugins/tabMgr';
import platform from '../plugins/platform';
import avatarCache from '../plugins/avatarCache';

export default {
  state: {
    list: [],
    oldUnreadList: null
  },

  mutations: {
    updateTicketList(state, { list }) {
      state.list = list;
      platform.send('update-badge', list.length || null);
      const newUnreadList = state.list.filter(v => v.unread);
      window.document.title = `TK2 [${list.length}/${newUnreadList.length}]`;

      // Уведы
      if (!state.oldUnreadList) {
        state.oldUnreadList = newUnreadList.map(v => v.id);
      }

      const newUnreads = newUnreadList.filter(v => !state.oldUnreadList.includes(v.id));
      newUnreads.forEach(async v => {
        const notification = new Notification(`#${v.ticket} ${v.name}`, {
          body: v.client,
          silent: true,
          icon: avatarCache.getAvatar(v.client)
        });

        const audio = new Audio('/sounds/notify.mp3');
        audio.play();

        platform.send('flash');
        notification.onclick = (() => {
          tabMgr.open('Ticket', { ticketId: v.ticket, elId: v.id });
          platform.send('focus');
        });
      });

      state.oldUnreadList = newUnreadList.map(v => v.id);
    }
  }
};
