import fetch from 'node-fetch';
import crypto from 'crypto';
// import { URLSearchParams } from 'url';
export class ApiError extends Error {
    constructor(slug, message, data) {
        super(message);
        this.slug = slug;
        this.message = message;
        this.data = data;
    }
    toJson() {
        return JSON.stringify({
            error: {
                slug: this.slug,
                message: this.message,
                data: this.data
            }
        });
    }
}
export default class UarhApiClient {
    constructor(options) {
        this.options = options;
        [this.authCode, this.secret] = options.token.split(':');
    }
    createSignature(route, method, authCode, body, apiSecret) {
        return crypto
            .createHmac('sha256', apiSecret)
            .update(`${route}|${method}|${authCode}|${JSON.stringify(body || {})}`)
            .digest('hex');
    }
    async rawQuery(method, route, params = {}, body = {}) {
        const result = await fetch(`${this.options.baseUrl}${route}?${new URLSearchParams(params)}`, {
            method,
            // body: method !== 'get' && JSON.stringify(body)
            headers: {
                'Content-Type': 'application/json'
            },
            ...(method === 'get' ? {} : { body: JSON.stringify(body) })
        }).then(v => v.json());
        if (result.error) {
            throw new ApiError(result.error.slug, result.error.message, result.error.data);
        }
        return result.data;
    }
    async query(method, route, body = {}) {
        const parsedRoute = Array.isArray(route) ? route.join('/') : route;
        const signature = this.createSignature(parsedRoute, method, this.authCode, body, this.secret);
        return this.rawQuery(method, parsedRoute, {
            auth: this.authCode,
            signature
        }, body);
    }
}
