import Vue from 'vue';
import parser from 'fast-xml-parser';
import he from 'he';
import IpService from './ipService.js';
import store from '../store';

class IspApi {
  constructor() {
    this.status = 'not_authorized';
    this.ipService = new IpService(this);
    this.url = 'https://api.tk2.u14t.ru';
    this.userInfo = null;

    if (this.token) {
      this.checkToken();
    }

    setInterval(() => this.updateTicketList(), 10e3);
  }

  get token() {
    return localStorage.apiToken;
  }

  set token(newValue) {
    localStorage.apiToken = newValue;
  }

  setStatus(newStatus) {
    this.status = newStatus;
    if (newStatus === 'authorized') {
      this.updateTicketList();
      this.getDesktop();
    }
  }

  async updateTicketList() {
    if (this.status !== 'authorized') {
      return;
    }

    const list = await this.exec('ticket');
    store.commit({ type: 'updateTicketList', list });
  }

  async checkToken() {
    this.setStatus('token_check');
    const result = await this.query({ auth: this.token, func: 'ticket' });
    if (result.error) {
      if (result.error.detail === "У вас недостаточно прав на выполнение функции '__value__'") {
        console.warn('Сессия BillManager истекла. Переподключение...');
        this.setStatus('not_authorized');
        return false;
      }

      throw Error(result.error.msg);
    }

    this.setStatus('authorized');
    return true;
  }

  async getDesktop() {
    if (!this.token) {
      return;
    }

    this.desktop = await this.func('desktop');// .catch(() => false);
    console.log(this.desktop);
    if (!this.desktop) {
      return;
    }

    this.userInfo = {
      realname: this.desktop.user.attr['@_realname'],
      name: this.desktop.user.attr['@_name'],
      avatar: this.desktop.user.attr['@_avatar_src']
    };
  }

  async auth(options) {
    console.log('Авторизация в BillManager...');
    const result = await this.query({
      func: 'auth',
      username: options.username,
      password: options.password,
      out: 'xml'
    });

    if (result.error) {
      throw Error(`Auth error: ${result.error.msg}`);
    }

    this.token = result.auth;
    return result.auth;
  }

  async getCaptcha() {
    const result = await this.query({
      func: 'logon',
      out: 'xml'
    });

    return result.recaptcha_field && {
      recaptcha_field: result.recaptcha_field,
      recaptcha_type: result.recaptcha_type
    };
  }

  exec2(func, params = {}) {
    return fetch(`${this.url}/api2/billmgr/${func}?${new URLSearchParams({ auth: this.token, ...params }).toString()}`).then(v => v.json());
  }

  async exec(func, params = {}) {
    return this.rawJson({ func, auth: this.token, ...params });
  }

  rawJson(params = {}) {
    return fetch(`${this.url}?${new URLSearchParams({ out: 'JSONdata', ...params }).toString()}`, {
      headers: new Headers({
        'User-Agent': 'Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/91.0.4472.124 Safari/537.36 Edg/91.0.864.64'
      })
    }).then(v => v.text())
      .then(v => JSON.parse(v.replace(/\\'/g, '\'')));
  }

  async query(params = {}) {
    const response = await fetch(`${this.url}?${new URLSearchParams({ out: 'xml', ...params }).toString()}`, {
      headers: {
        'User-Agent': 'Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/91.0.4472.124 Safari/537.36 Edg/91.0.864.64'
      }
    }).then(v => v.text());
    if (!params.out || params.out === '' || params.out === 'xml') {
      return parser.parse(response, {
        attributeNamePrefix: '@_',
        attrNodeName: 'attr',
        ignoreAttributes: false,
        tagValueProcessor: v => he.decode(v, { useNamedReferences: true }), // default is a=>a
        attrValueProcessor: v => he.decode(v, { isAttributeValue: true, useNamedReferences: true })// default is a=>a
      }).doc;
    }

    if (params.out === 'bjson' || params.out === 'JSONdata') {
      return JSON.parse(response.replace(/\\'/g, '\''));
    }

    return response;
  }

  async raw(params = {}) {
    const result = await this.query({ auth: this.token, ...params });
    if (result.error) {
      if (result.error.code === 'access' && params.func === 'ticket') {
        console.warn('Сессия BillManager истекла. Переподключение...');
        return this.core.validateAuth();
      }

      throw Error(result.error.msg);
    }

    return result;
  }

  async func(func, params = {}) {
    return this.raw({ func, ...params });
  }

  async array(func, params = {}) {
    const response = await this.raw({ func, ...params });
    if (response.list) {
      return response.list.elem;
    }

    if (!response.elem) {
      return [];
    }

    return Array.isArray(response.elem) ? response.elem : [response.elem];
  }

  forceArray(obj) {
    return Array.isArray(obj) ? obj : [obj];
  }

  async findFiltered(func, filter) {
    await Promise.all([
      this.exec('tsetting', { sok: 'ok', elid: func, ratelimit: 200 }),
      await this.exec(`${func}.filter`, { sok: 'ok', drop: 'on' })
    ]);

    await this.exec(`${func}.filter`, {
      sok: 'ok',
      ...filter
    });

    console.log('...');
    const response = await this.exec(func, { sok: 'ok', out: 'html' });
    console.log('parse filtered...');
    return {
      headers: Object.fromEntries(response.headers.map(v => [v.name, v.pName])),
      data: response.content.map(v => Object.fromEntries(Object.entries(v).map(b => [b[0], b[1].v]).filter(b => b[1])))
    };
  }

  emailByName(name) {
    const mathed = name.match(/.+ \((.+)\)/);
    return mathed ? mathed[1] : name;
  }

  decodeHTMLEntities(text) {
    const entities = {
      amp: '&',
      apos: '\'',
      '#x27': '\'',
      '#x2F': '/',
      '#39': '\'',
      '#47': '/',
      lt: '<',
      gt: '>',
      nbsp: ' ',
      quot: '"'
    };

    return text.replace(new RegExp(`&?:(${Object.keys(entities).join('|')});`, 'g'), v => entities[v]);
  }
}

const api = new IspApi();
Vue.mixin({
  data: () => ({ api })
});

export default api;
