<template>
<v-app id="inspire" v-if="core.state.type !== 'main'" :class="core.mobile ? 'mobile' : 'desktop'">
  <SystemBar v-if="platform.type === 'electron'"/>
  <Auth v-if="core.state.type === 'auth'"/>
  <Loading v-else/>
</v-app>
<v-app id="inspire" v-else :class="core.mobile ? 'mobile' : 'desktop'">
  <SystemBar v-if="platform.type === 'electron'"/>
  <AppBar @reload="reload"/>
  <TabRenderer/>
</v-app>
</template>

<style>
.v-application, .v-sheet {
  background-color: var(--v-background-base) !important;
}

.v-card, .v-navigation-drawer, .v-data-table, .v-skeleton-loader, .v-skeleton-loader > div {
  background-color: var(--v-cards-base) !important;
}

.v-menu__content .v-list {
  background-color: var(--v-cards-darken1) !important;
}

.v-list {
  background: rgba(0,0,0,0.1) !important;
}

.v-avatar {
  overflow: visible !important;
}

.a-title {
  text-decoration: none;
  font-weight: bold;
  color: var(--v-text-base) !important;
}

.a-title:hover {
  color: var(--v-text-darken1) !important;
}

.v-list-item:hover .v-icon {
  font-size: 30px;
}

body, html, #app {
  overflow-y: hidden !important;
  overflow-x: hidden !important;
}

.fade-enter-active, .fade-leave-active {
    transition: opacity .5s
}

.fade-enter, .fade-leave-to {
    opacity: 0
}

/* Enter and leave animations can use different */
/* durations and timing functions.              */
.slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}
</style>

<script>
import Auth from '@/render/views/Auth.vue';
import AppBar from '@/render/components/Common/AppBar.vue';
import Loading from '@/render/views/Loading.vue';
import TabRenderer from '@/render/components/Common/TabRenderer.vue';

export default {
  components: {
    SystemBar: () => import('@/render/components/Common/SystemBar.vue'),
    Auth,
    Loading,
    AppBar,
    TabRenderer
  },
  name: 'TK2',
  data() {
    return {
      transitionName: 'fade-in'
    };
  },

  created() {
    const { dark } = this.$store.state.persist;
    this.$vuetify.theme.dark = dark;
  },

  methods: {
    reload() {
      const tab = this.tabMgr.get();
      const oldLocation = tab.location;
      if (tab) {
        delete this.tabMgr.get().saved;
        tab.location = 'reloading';
      }

      const { index } = this.tabMgr;
      this.tabMgr.index = -1;
      process.nextTick(() => {
        this.tabMgr.index = index;
        if (tab) {
          tab.location = oldLocation;
        }
      });
    }
  },

  computed: {
    component() {
      return this.tabMgr.list[this.tabMgr.index] ? this.tabMgr.list[this.tabMgr.index].component : 'Null';
    },
    location() {
      return document.location.href;
    }
  }/* ,
  watch: {
    $route (to, from) {
      const toDepth = to.path.split('/').length
      const fromDepth = from.path.split('/').length
      this.transitionName = toDepth < fromDepth ? 'slide-left' : 'slide-right'
    }
  } */
};
</script>
