import Vue from 'vue';
import store from '../store';
import api from './api';

class AvatarCache {
  getAvatar(client) {
    const email = api.emailByName(client);
    return store.state.persist.avatarCache[email]
      ? `https://my.msk.host/manimg/userdata/avatar_files/${store.state.persist.avatarCache[email]}`
      : 'https://my.msk.host/manimg/userdata/img/customer.png';
  }

  setAvatar(client, avatar) {
    const [_, data] = avatar.match(/avatar_files\/(.+)$/) || [];
    if (!data) {
      return;
    }

    const email = api.emailByName(client);
    store.commit({ type: 'setAvatar', client: email, avatar: data });
  }
}

const avatarCache = new AvatarCache();
Vue.mixin({
  data: () => ({ avatarCache })
});

export default avatarCache;
