  const { cache, keys, _vnode } = keepAliveInstance
  const entries = Object.entries(cache).filter(v => v[1])
  const toDelete = entries.filter(v => !filter(v[0])) // By key
  // console.log('TO delete ', toDelete)
  toDelete.forEach(v => pruneCacheEntry(cache, v[0], keys, _vnode))
