<template>
  <v-main style="height: 100%">
    <v-layout column justify-center align-center fill-height>
      <v-flex xs3><h1 class="display-3">TK2</h1></v-flex>

      <v-flex xs4 text-center>
    <div class="content">
      <v-form
        ref="form"
        lazy-validation
      >
        <v-text-field
          name="login"
          v-model="username"
          label="Логин"
          required
        />
        <v-text-field
          name="password"
          v-model="password"
          type="password"
          label="Пароль"
          required
        />
        <v-text-field
          name="tkb-token"
          v-model="tkbToken"
          type="password"
          label="Токен TKB"
          required
        />

        <vue-recaptcha v-if="captcha" :sitekey="captcha.recaptcha_field"></vue-recaptcha>

        <v-btn
          color="success"
          class="mr-4"
          @click="auth"
        >Войти</v-btn>
      </v-form>
    </div>
      </v-flex>
    </v-layout>
  </v-main>
</template>

<script>
import VueRecaptcha from 'vue-recaptcha';

export default {
  name: 'Auth',

  components: { VueRecaptcha },

  data: () => ({
    captcha: null,
    username: localStorage.username || '',
    password: localStorage.password || '',
    tkbToken: localStorage.tkbToken || ''
  }),

  async mounted() {
    this.captcha = await this.api.getCaptcha();
  },

  methods: {
    async auth() {
      localStorage.username = this.username;
      localStorage.password = this.password;
      localStorage.tkbToken = this.tkbToken;

      try {
        await this.authApi();
        await this.authTkb();
      } catch (error) {
        this.api.setStatus('not_authorized');
        this.tkb.status = 'not_authorized';
        // eslint-disable-next-line no-alert
        alert(error.message);
      }
    },

    async authApi() {
      this.api.setStatus('authorization');
      const result = await this.api.query({
        func: 'auth',
        username: this.username,
        password: this.password,
        out: 'bjson'
      });

      if (result.error) {
        throw Error(`Ошибка во время авторизации: ${result.error.msg}`);
      }

      this.api.token = result.model.auth;
      this.api.setStatus('authorized');
    },

    async authTkb() {
      this.tkb.token = this.tkbToken;
      this.tkb.status = 'authorization';
      await this.tkb.query('put', 'staff/shift')
        .then(() => true);

      this.tkb.status = 'authorized';
    }
  }
};
</script>
