import Vue from 'vue';

class Platform {
  constructor() {
    this.type = (window && window.process && window.process.type) ? 'electron' : 'web';
    
    import(`./${this.type}Platform.js`)
      .then(v => { this.realization = v['default'] });
  }

  send(channel, ...args) {
    if (!this.realization) {
      return;
    }

    this.realization.send(channel, ...args);
  }

  openPanelWindow(url, sessionId) {
    if (!this.realization) {
      return;
    }
    this.realization.openPanelWindow(url, sessionId);
  }

  openUrl(url) {
    if (!this.realization) {
      return;
    }

    this.realization.openUrl(url);
  }
}

const platform = new Platform();
Vue.mixin({
  data: () => ({ platform })
});

export default platform;
